import Vue from 'vue'
import VueRouter from 'vue-router'

import home from '../views/home.vue';//主页
import blog from '../views/blog.vue';//博客
import walkthrough from '../views/walkthrough.vue';//攻略
import journalPage from '../views/journal-page.vue';//日志
import about from '../views/about.vue';//关于我

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: '/home'
    },
    {
        path: '/home',
        name: 'home',
        component: home
    },
    {
        path: '/blog',
        name: 'blog',
        component: blog
    },
    {
        path: '/walkthrough',
        name: 'walkthrough',
        component: walkthrough
    },
    {
        path: '/journalPage',
        name: 'journalPage',
        component: journalPage
    },
    {
        path:'/about',
        name:'about',
        component:about
    }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router
