<template>
    <!--动画标签包裹-->
    <transition name="move">
        <!--游戏详情界面包装-->
        <div v-show="gameShow" class="games" ref="games" :class="selectedGame.type">
            <div class="game-top" :class="selectedGame.type">
                <!--退出按钮-->
                <div class="iconfont icon-icon-cross-squre" @click="close"></div>
                <!--标题-->
                <h1>《{{selectedGame.name}}》</h1>
            </div>
            <!--内容-->
            <div class="game-content">
                <!--短语-->

                <p>
                    {{selectedGame.details.text}}
                </p>
            </div>
            <!--图-->
            <div class="game-pic" :class="selectedGame.type"></div>
        </div>
    </transition>


</template>

<script>
    export default {
        name: '',
        data(){
            return{
                gameShow:false,
            }
        },
        methods:{
            show(){
                this.gameShow = true;
            },
            close(){
                this.gameShow = false;
            }
        },
        props:["selectedGame"]
    }
</script>

<style lang="scss" scoped>
    .games{
        width:100%;
        position: fixed;
        top:0;
        left:0;
        bottom:0;
        right:0;
        overflow-y: scroll;
        z-index:199;
        &.hssm{
            background:url("./hssm-bg.jpg") no-repeat;
            background-size:cover;
        }
        &.lol{
            background:url("./lol-bg.jpg") no-repeat;
            background-size:cover;
        }
        &.sy{
            background:url("./sy-bg.jpg") no-repeat;
            background-size:cover;
        }
        &.wm6{
            background:url("./wm6-bg.jpg") no-repeat;
            background-size:cover;
        }
        &.mhxy{
            background:url("./mhxy-bg.jpg") no-repeat;
            background-size:cover;
        }
        .game-top{
            width:100%;
            height:400px;
            text-align: center;
            position:relative;
            display: flex;
            align-items: center;
            justify-content: center;
            &.hssm{
                background:url("./hssm-bg1.png") no-repeat 0 40%;
                background-size:cover;
            }
            &.lol{
                background:url("./lol-bg1.jpg") no-repeat 0 40%;
                background-size:cover;
            }
            &.sy{
                background:url("./sy-bg1.jpg") no-repeat 0 40%;
                background-size:cover;
            }
            &.wm6{
                background:url("./wm6-bg1.jpg") no-repeat 0 40%;
                background-size:cover;
            }
            &.mhxy{
                background:url("./mhxy-bg1.jpg") no-repeat 0 40%;
                background-size:cover;
            }
            /*退出按钮*/
            .iconfont{
                font-size:40px;
                position:absolute;
                top:40px;
                right:80px;
                transition: all 1s;
                color:white;
                cursor: pointer;
                &:hover{
                    transform: rotate(180deg);
                }
            }
            h1{
                cursor: default;
                height:120px;
                line-height: 120px;
                white-space:nowrap;
                //border:1px solid black;
                background-color: #343434;
                color: #eeeeee;
                font-size:60px;
                padding:0 20px;
                box-shadow:0 1px 5px 2px #161616;

                //淡入动画
                opacity: 0;
                animation: 0.5s 0.5s forwards fade_in;

            }
        }
        /*内容*/
        .game-content{
            width:60%;
            background-color:#eeeeee;
            color:#343434;
            padding:50px;
            margin:0 auto;
            border-radius: 50px 50px 0 0 ;

            //淡入动画
            opacity: 0;
            animation: 0.75s 0.75s forwards fade_in;

            h2{
                text-align: center;
                font-size:30px;
                margin-bottom:20px;
            }
            p{
                font-size:17px;
                text-indent: 2em;
            }
        }
        /*图*/
        .game-pic{
            width:60%;
            height:500px;
            margin:0 auto 50px;
            &.hssm{
                background:url("./hssm-bg2.jpg") no-repeat;
                background-size:cover;
            }
            &.lol{
                background:url("./lol-bg2.jpg") no-repeat;
                background-size:cover;
            }
            &.sy{
                background:url("./sy-bg2.jpg") no-repeat;
                background-size:cover;
            }
            &.wm6{
                background:url("./wm6-bg2.jpg") no-repeat;
                background-size:cover;
            }
            &.mhxy{
                background:url("./mhxy-bg2.jpg") no-repeat;
                background-size:cover;
            }
            //淡入动画
            opacity: 0;
            animation: 0.9s 0.9s forwards fade_in;
        }
    }

    /*淡入动画*/
    @keyframes fade_in {
        100% {
            opacity: 1;
        }
    }

    /*游戏详情界面淡入淡出动画*/
    .move-enter-active{
        animation: ani 0.8s;
    }
    .move-leave-active{
        animation: ani 0.8s reverse;
    }
    @keyframes ani {
        from {transform:translate3d(0,-10%,0);opacity: 0}
        to {transform:translate3d(0,0,0);opacity: 1}
    }
</style>