<template>
<div class="page-wrapper">
    <!--导航-->
    <v-nav></v-nav>
    <div class="wrapper">
        <!--背景顶图-->
        <div class="top">
            <div class="top-style">
                <div class="style-wrapper">
                    <div class="style-title">
                        <div class="title-text">{{blog.name}}</div>
                    </div>
                    <div class="style-line"></div>
                </div>
            </div>
        </div>
        <div class="blog">
            <!--blog组件包装-->
            <div class="blog-wrapper">
                <blog-list :blog="blog" @selectedgame="selectedgame"></blog-list>
                <!--博客标签-->
                <v-label class="label"></v-label>
            </div>


        </div>

    </div>

    <!--透明页脚-->
    <div class="foot-tra"></div>
    <!--滑出式页脚-->
    <v-footer class="foot" :home="home"></v-footer>
    <!--传递选中的游戏给game组件-->
    <game :selected-game="selectedGame" ref="games"></game>
</div>
</template>

<script>
    import nav from '../components/nav/nav.vue';//导航栏
    import foot from '../components/foot/foot.vue';//引入尾部
    import blogList from '../components/bolglist/bloglist.vue';//引入博客列表
    import contactLabel from '../components/contact-label/contact-label.vue';//引入标签
    import game from '../components/game/game.vue';//引入游戏详情页


    export default {
        name: '',
        data(){
            return{
                selectedGame:[]//定义空数组存放点选的游戏
            }
        },
        props:["home","blog"],
        components:{
            "v-nav":nav,//注册导航标签
            'v-footer':foot,//注册底部标签
            'blog-list': blogList,//注册博客列表标签
            'v-label':contactLabel,//注册标签
            game
        },
        methods:{
            /*自定义事件 监听子组件传来的点选的游戏 game，添加给selectedGame数组 再传递到子组件game里*/
            selectedgame(game){
                this.selectedGame = game;
                this.$refs.games.show();//调用子组件ref="games" 里的show()方法
            }
        }
    }
</script>

<style lang="scss" scoped>
    .page-wrapper {
        width: 100%;
        position:absolute;
        top:55px;

        .wrapper {
            width: 100%;

            /*顶部图片和样式*/
            .top{
                width:100%;
                height:450px;
                background:url('./lol.jpg') no-repeat 0 30%;
                background-size:cover;
                position:relative;
                /*顶部样式*/
                .top-style{
                    width:100%;
                    position:absolute;
                    bottom:0;
                    left:0;
                    /*横线包装*/
                    .style-wrapper{
                        width:1300px;
                        margin:0 auto;
                        .style-title{
                            width:100%;
                            height:60px;
                            line-height: 60px;
                            background-color:rgba(0,0,0,0.8);
                            padding-left:20px;
                            //滑入动画
                            opacity: 0;
                            transform: translate(-50vh, 0);
                            animation: 0.5s 0.5s forwards slide_in;
                            .title-text{
                                color:white;
                                font-size:25px;
                            }
                        }
                        .style-line{
                            width:100%;
                            height:20px;
                            background-color: #10a0ff;

                            //滑入动画
                            opacity: 0;
                            transform: translate(50vh, 0);
                            animation: 0.3s 0.3s forwards slide_in;
                        }
                    }
                }
            }
            /*博客内容*/
            .blog{
                width:100%;
                background-color: #eeeeee;
                min-height:1000px;
                padding-bottom:100px;
                position:relative;
                /*区域*/
                .blog-wrapper{
                    width:1300px;
                    height:100%;
                    margin:0 auto;
                    display: flex;
                    justify-content: space-between;
                    //background-color: #f5f5f5;
                    //box-shadow:0 1px 5px 1px #b9b9b9;

                    //淡入动画
                    opacity: 0;
                    animation: 0.75s 0.75s forwards fade_in;

                    .label{
                    }
                }
                /*粒子特效*/
                #particles-js{
                    width: 100%;
                    height: calc(100% - 100px);
                    position: absolute;   //设置absolute,其他DIV设置为relative，这样这个例子效果就作为背景
                }

            }

        }
    }
    /*透明页脚 做滑出式页脚用*/
    .foot-tra{
        width:100%;
        height:280px;
    }
    /*滑出式页脚*/
    .foot{
        position:fixed;
        bottom:0;
        left:0;
        z-index:-1;
    }

    /*淡入动画*/
    @keyframes fade_in {
        100% {
            opacity: 1;
        }
    }

    /*滑入动画*/
    @keyframes slide_in {
        100% {
            transform: translate(0, 0);
            opacity: 1;
        }
    }
</style>