<template>
    <div>
        <ul class="blog-list" v-if="blog.game">
            <li class="list-content" v-for="(item,index) in blog.game" :key="index">
                <img :src="item.img" width="375px" height="250px">
                <div class="list-item">
                    <h1>《{{item.name}}》</h1>
                    <p>
                        {{item.info}}
                    </p>
                </div>
                <div class="list-link">
                    《{{item.name}}》
                </div>
            </li>
        </ul>
        <div class="">
            <div class=""></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props:["blog"],
        methods:{
            selectGame(game){
                this.selectedGame = game;
                this.$refs.games.show();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .blog-list{
        flex:1;
        width:100%;
        height:100%;
        list-style:none;
        padding:30px 0 50px;

        /*li*/
        .list-content{
            width:900px;
            height:250px;
            //border-radius:40px;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            margin:0 0 50px;
            box-sizing:border-box;
            transition: all 0.5s;
            position:relative;

            //滑入动画
            transform: translate(0, 50vh);
            opacity: 0;
            animation: 1s 1s forwards slide_in;

            img{
                //flex:0;
                transition: all 0.5s;
            }
            &:last-child {
                margin-bottom: 0;
            }
            /*鼠标移入li*/
            &:hover{
                box-shadow:0 3px 10px 2px;
                img{
                    filter:blur(5px);
                }
            }
            .list-item{
                width:100%;
                height:100%;
                background-color:white;
                padding:25px;
                transition: all 0.5s;
                border-right:10px solid #f6b43a;
                box-sizing:border-box;
                &:hover{
                    border-right:500px solid #f6b43a;
                }

                h1{
                    font-size:21px;
                    cursor:default;
                    text-overflow: ellipsis; /*文本溢出时显示省略号*/
                    overflow: hidden;
                    white-space:nowrap;
                }
                p{
                    text-overflow: ellipsis; /*文本溢出时显示省略号*/
                    overflow: hidden;
                    text-indent: 2em;
                    margin-top:18px;
                    font-size:16px;
                    cursor:default;
                    -webkit-line-clamp:6;//设置超出多少行隐藏
                    -webkit-box-orient:vertical;
                    //设置 display 为 -webkit-box 或者 -webkit-inline-box 时为隐藏状态
                    display: -webkit-box;
                }
                &:hover + .list-link{
                    opacity: 1;
                }
            }

            /*名字链接*/
            .list-link{
                font-size:30px;
                position:absolute;
                right:65px;
                opacity: 0;
                transition: all 0.6s;
                //cursor: pointer;
                pointer-events: none;//阻止了hover事件，可能链接也得设置在 list-item 或者 list-content 上
            }


        }
    }

    /*li滑入动画*/
    @keyframes slide_in {
        100% {
            transform: translate(0, 0);
            opacity: 1;
        }
    }

</style>