import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import {http} from './server/server.js'

import ElementUI from 'element-ui';//element引入
import 'element-ui/lib/theme-chalk/index.css';//element引入

import VueParticles from 'vue-particles'//粒子动画引入

import ViewUI from 'view-design';//iview引入
import 'view-design/dist/styles/iview.css';//iview引入

Vue.use(ViewUI);//iview引入

Vue.use(VueParticles);//粒子动画

Vue.use(ElementUI);//element引入

Vue.config.productionTip = false;
Vue.prototype.$http = http;
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
